// index.js

import '../styles/output.css';

document.addEventListener('DOMContentLoaded', () => {
  const form = document.getElementById('weather-form');
  const input = document.getElementById('location-input');

  form.addEventListener('submit', async (e) => {
    e.preventDefault();
    const location = input.value.trim();
    if (!location) return;

    // Lazy-load dependencies only when needed
    const [
      { fetchWeather },
      { processWeatherData },
      { showLoading, hideLoading },
      { displayWeather },
    ] = await Promise.all([
      import('./modules/api.js'),
      import('./modules/processWeatherData.js'),
      import('./modules/loading.js'),
      import('./modules/displayWeather.js'),
    ]);

    showLoading();

    try {
      const rawData = await fetchWeather(location);
      const weather = processWeatherData(rawData);

      displayWeather(weather);
    } catch (error) {
      console.error('Error fetching or processing weather:', error);
      alert('Failed to fetch weather data.');
    }

    hideLoading();
  });
});
